}

.confirm-bttn {
  text-decoration: none;
  color: white;
  background-color: dodgerblue;
  border-radius: 6px;
  padding: 6px 11px;
  font-family: Lato, serif;
  font-size: 21px;
  text-align: center;
  margin-top: 1em;
  letter-spacing: 1px;
  transition: 0.3s;
}

.confirm-bttn:hover {
  background-color: rgb(90, 223, 90);
  letter-spacing: 3px;
}